import { useState } from 'react'
import StandardInput from '../input/StandardInput'

const defaultFormData = {
  name: {
    value: ''
  },
  phone: {
    value: ''
  }
}

const schemaFields = ['name', 'phone']

const StandardForm = ({
  formData = defaultFormData,
  schemaFields = schemaFields
}) => {
  const [currentFormData, setCurrentFormData] = useState(null)
  const handleSetFormData = (fieldName, value) => {
    const updatedFormData = {
      ...currentFormData,
      [fieldName]: value
    }
    setFormData(updatedFormData)
  }
  return (
    <form>
      {schemaFields.map(field => (
        <StandardInput
          name={field.name}
          placeholder={field.name}
          value={currentFormData[field.name].value}
          onChange={value => handleSetFormData(field.name, value)}
        />
      ))}
    </form>
  )
}

export default StandardForm
